<script lang="ts">
  import { onMount } from 'svelte';
  import { goto } from '$app/navigation';
  import { page } from '$app/stores';
  import { isAuthenticated, currentUser, isLoading } from '$lib/stores/authStore';
  import { Shield, AlertTriangle } from 'lucide-svelte';

  // Props
  export let requireAuth = true;
  export let requiredRole: string | null = null;
  export let requiredPermission: string | null = null;
  export let fallbackPath = '/login';

  // State
  let isAuthorized = false;
  let authorizationError = '';

  // Check authorization whenever auth state or user changes
  $: checkAuthorization($isAuthenticated, $currentUser);

  function checkAuthorization(authenticated: boolean, user: any) {
    // Reset state
    isAuthorized = false;
    authorizationError = '';

    // If route doesn't require auth, allow access
    if (!requireAuth) {
      isAuthorized = true;
      return;
    }

    // If not authenticated and auth is required
    if (!authenticated) {
      authorizationError = 'Authentication required';
      if (!$isLoading) {
        goto(fallbackPath);
      }
      return;
    }

    // If authenticated but no user data yet (shouldn't happen)
    if (!user) {
      authorizationError = 'User data not available';
      return;
    }

    // Check role requirements
    if (requiredRole) {
      if (user.role !== requiredRole && user.role !== 'super_admin') {
        authorizationError = `Access denied. Required role: ${requiredRole}`;
        return;
      }
    }

    // Check permission requirements
    if (requiredPermission) {
      if (!hasPermission(user, requiredPermission)) {
        authorizationError = `Access denied. Required permission: ${requiredPermission}`;
        return;
      }
    }

    // All checks passed
    isAuthorized = true;
  }

  // Permission checking logic
  function hasPermission(user: any, permission: string): boolean {
    if (!user) return false;

    // Super admin has all permissions
    if (user.role === 'super_admin') return true;

    // Role-based permissions
    switch (user.role) {
      case 'client_admin':
        return [
          'client:read', 'client:write', 
          'routing:read', 'routing:write', 
          'branding:read', 'branding:write', 
          'response_times:read', 'response_times:write',
          'ai_prompts:read', 'ai_prompts:write',
          'users:read', 'users:write'
        ].includes(permission);
      
      case 'client_user':
        return [
          'client:read', 
          'routing:read', 
          'branding:read',
          'response_times:read',
          'ai_prompts:read'
        ].includes(permission);
      
      case 'api_user':
        return ['client:read'].includes(permission);
      
      default:
        return false;
    }
  }

  onMount(() => {
    // Perform initial authorization check
    checkAuthorization($isAuthenticated, $currentUser);
  });
</script>

{#if $isLoading}
  <!-- Loading state -->
  <div class="min-h-screen bg-gray-50 flex items-center justify-center">
    <div class="text-center">
      <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
      <p class="mt-4 text-gray-600">Checking authentication...</p>
    </div>
  </div>
{:else if authorizationError}
  <!-- Authorization error -->
  <div class="min-h-screen bg-gray-50 flex items-center justify-center p-4">
    <div class="max-w-md w-full bg-white rounded-lg shadow-lg p-6 text-center">
      <div class="mx-auto h-12 w-12 bg-red-100 rounded-full flex items-center justify-center mb-4">
        <AlertTriangle class="h-6 w-6 text-red-600" />
      </div>
      
      <h2 class="text-xl font-semibold text-gray-900 mb-2">
        Access Denied
      </h2>
      
      <p class="text-gray-600 mb-4">
        {authorizationError}
      </p>
      
      <button
        on:click={() => goto(fallbackPath)}
        class="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
      >
        <Shield class="h-4 w-4 mr-2" />
        Go to Login
      </button>
    </div>
  </div>
{:else if isAuthorized}
  <!-- Authorized - render children -->
  <slot />
{:else}
  <!-- Fallback loading -->
  <div class="min-h-screen bg-gray-50 flex items-center justify-center">
    <div class="text-center">
      <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
      <p class="mt-4 text-gray-600">Loading...</p>
    </div>
  </div>
{/if}

<style>
  .animate-spin {
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style> 